@use '../../utils/themeConstants';

.skills-list__search-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  gap: 10px;
}

.skills-list__search-container__search-field {
  flex: 1;
  padding: 10px;
  height: 40px;
  
  border-radius: 10px;
  border: 0px;
  
  color: themeConstants.$color-dark;
  background-color: themeConstants.$color-bright;
  outline-offset: 0px;
  font-size: 18px;

  &:focus {
    outline: 2px solid themeConstants.$color-medium;
  }

  @media (hover: hover) {
    &:hover {
      outline: 2px solid themeConstants.$color-medium;
    }
  }
}

.skills-list__search-container__search-reset-button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  width: 40px;
  
  border-radius: 10px;
  border: 0px;
  
  color: themeConstants.$color-dark;
  background-color: themeConstants.$color-bright;
  font-size: 18px;

  @media (hover: hover) {
    &:hover {
      outline: 2px solid themeConstants.$color-medium;
      cursor: pointer;
    }
  }
}

.skills-list__items {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.skills-list__items__skill-set {

}

.skills-list__items__skill-set__title {
  font-size: 24px;
  font-weight: 700;
  line-height: 40px;
}

.skills-list__items__skill-set__skill {
  font-size: 16px;
  white-space: pre-wrap;
}

.skills-list__items__search-results-container {
  transition: all 500ms ease-in-out;
  overflow: hidden;
}

.skills-list__items__search-result {
  font-size: 18px;
  line-height: 25px;
  color: rgb(20, 146, 20);
}

.skills-list__items__search-results-empty {
  font-size: 18px;
  line-height: 25px;
}

.skills-list__items__search-results-empty__reset-results-button {
  cursor: pointer;
  text-decoration: underline !important;
  border: 0px;
  background: none;
  margin: 0;
  padding: 0;

  font-family: Consolas;
  font-size: 18px;
  color: themeConstants.$color-dark;

  &:hover {
    color: themeConstants.$color-dark-highlight;
  }
}